<template>
  <div class="myTeam">
    <van-tabs v-model="active" v-if="$route.query.type == 1" :line-width="20" :line-height="3" :swipe-threshold="6" :title-active-color="'#ef5852'" @click="onClick">
      <van-tab v-for="(item,index) in classifyList" :key="index" :title="item.label"></van-tab>
    </van-tabs>
    <div class="mid1" id="main" v-show="active == 0"></div>
    <div class="list mescroll" id="list" v-show="active == 1">
      <div class="list-data" id="listData">
        <div class="item" v-for="(item,index) in list" :key="index">
          <div class="flex">
            <!-- <div class="left">
              <img v-if="index<0" :src="require('@/assets/img/icon/crown'+ (index + 1 )+'.png')" alt />
              <div v-else class="index">一阶会员</div>
            </div> -->
            <div class="mid">
              <img :src="$utils.dealImg(item.userHeader2)" alt />
            </div>
            <div class="right">
              <div class="right-left">
                <div class="name">{{item.userName2}}
                  <!-- <span @click="call(item.phone)">{{item.phone}}</span> -->
                </div>
                <div class="time">加入时间:{{item.createDate + ":00"}}</div>
              </div>
              <!-- <div class="right-right">
                <div class="label">已下单数</div>
                <div class="num">{{item.total_order_num}}</div>
              </div> -->
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { Tab, Tabs } from "vant";
import { getActivityTeamList } from "@/api";
import echarts from "echarts";
import MeScroll from "mescroll.js";
export default {
  components: {
    [Tab.name]: Tab,
    [Tabs.name]: Tabs
  },
  data() {
    return {
      classifyList: [{ label: "关系网展示" }, { label: "列表网展示" }],
      mescroll: null,
      list: [],
      active: 0,
      myChart: null,
      links: [],
      list1: []
    };
  },
  methods: {
    onClick(name, title) {
      if (this.name == 0) {
        this.creatEchart();
      } else {
        if (this.mescroll) {
          this.mescroll.scrollTo(0, 0);
          this.mescroll.resetUpScroll();
        } else {
          this.startMescroll();
        }
      }
    },
    call(telno) {
      window.location.href = "tel:" + telno;
    },
    creatEchart() {
      let dataObj = {
        acode: this.$route.query.acode || "0",
        pageNum: 0,
        pageSize: 0
      };
      getActivityTeamList(dataObj).then(res => {
        if (res.code == 0) {
          this.myChart = echarts.init(document.getElementById("main"));
          var nodes = [];
          res.data.list.map((item, index) => {
            nodes.push({
              name: item.userName2,
              symbol: item.userHeader2,
              // telno: item.phone,
              symbolSize: 40
            });
          });
          nodes.push({
            name: sessionStorage.getItem("userName"),
            symbol: sessionStorage.getItem("headimgurl"),
            // telno: sessionStorage.getItem("telno"),
            symbolSize: 60
          });
          //创建links
          var links = [];
          nodes.map(item => {
            if (sessionStorage.getItem("userName") != item.name) {
              links.push({
                source: sessionStorage.getItem("userName"),
                target: item.name
              });
            }
          });
          this.links = links;
          console.log(nodes, links);
          var option = {
            tooltip: {
              trigger: "item",
              formatter: function(params) {
                // var str = params.data.name + "\n" + params.data.telno;
                var str = params.data.name;
                return str;
              }
            },
            animation: false,
            series: [
              {
                name: "",
                type: "graph",
                layout: "force",
                data: nodes,
                links: links,
                roam: false,
                itemStyle: {
                  background: "#000"
                },
                label: {
                  normal: {
                    position: "right",
                    formatter: function(params) {
                      // var str = params.data.name + "\n" + params.data.telno;
                      var str = params.data.name;
                      return str;
                    }
                  }
                },
                force: {
                  repulsion: 800
                }
              }
            ]
          };

          this.myChart.setOption(option);
          this.pubdata(nodes);
        } else {
          this.$toast.fail(res.msg);
        }
      });
    },
    getImgData(imgSrc) {
      var fun = function(resolve) {
        const canvas = document.createElement("canvas");
        const contex = canvas.getContext("2d");
        const img = new Image();
        img.crossOrigin = "";
        img.onload = function() {
          //设置图形宽高比例
          var center = {
            x: img.width / 2,
            y: img.height / 2
          };
          var diameter = img.width; //半径
          canvas.width = diameter;
          canvas.height = diameter;
          contex.clearRect(0, 0, diameter, diameter);
          contex.save();
          contex.beginPath();
          var radius = img.width / 2;
          contex.arc(radius, radius, radius, 0, 2 * Math.PI); //画出圆
          contex.clip(); //裁剪上面的圆形
          contex.drawImage(
            img,
            center.x - radius,
            center.y - radius,
            diameter,
            diameter,
            0,
            0,
            diameter,
            diameter
          ); // 在刚刚裁剪的园上画图
          contex.restore(); // 还原状态
          resolve(canvas.toDataURL("image/png", 1));
        };
        img.src = imgSrc;
      };
      var promise = new Promise(fun);
      return promise;
    },
    pubdata(json) {
      var androidMap = json;
      let that = this;
      var picList = []; //获取出全部图片
      for (var i = 0; i < androidMap.length; i++) {
        //把图片路径转成canvas
        let p = this.getImgData(androidMap[i].symbol);
        console.log(p);
        picList.push(p);
      }

      Promise.all(picList).then(function(images) {
        //取出base64 图片 然后赋值到jsondata中
        for (var i = 0; i < images.length; i++) {
          var img = "image://" + images[i];
          console.log(img);
          androidMap[i].symbol = img;
        } // 把数据设置到Echart中data

        that.myChart.setOption({
          series: [
            {
              data: androidMap,
              links: that.links
            }
          ]
        });
      });
    },
    loadData(pageNum, pageSize, successCallback, errorCallback) {
      let $this = this;
      let dataObj = {
        acode: this.$route.query.acode || "0",
        pageNum: pageNum,
        pageSize: pageSize
      };
      getActivityTeamList(dataObj)
        .then(res => {
          if (res.code == 0) {
            successCallback && successCallback(res.data);
          } else {
            $this.$toast.fail(res.msg);
          }
        })
        .catch(res => {
          errorCallback && errorCallback();
          console.log(res);
        });
    },
    startMescroll() {
      this.mescroll = new MeScroll("list", {
        //在vue的mounted生命周期初始化mescroll,确保此处配置的id能够被找到
        up: {
          callback: this.upCallback, //上拉回调
          //以下参数可删除,不配置
          isBounce: false, //此处禁止ios回弹,解析(务必认真阅读,特别是最后一点): http://www.mescroll.com/qa.html#q10
          page: { size: 10 }, //可配置每页8条数据,默认10
          empty: {
            //配置列表无任何数据的提示
            warpId: "listData",
            icon: require("@/assets/img/nodata.png"),
            tip: "暂无数据"
            //						  	btntext : "去逛逛 >" ,
            //						  	btnClick : function() {
            //						  		alert("点击了去逛逛按钮");
            //						  	}
          },
          noMoreSize: 10,
          scrollbar: { use: false },
          htmlNodata: '<p class="upwarp-nodata">-- 无更多数据 --</p>',
          lazyLoad: {
            use: true // 是否开启懒加载,默认false
          }
        }
      });
    },
    //上拉回调 page = {num:1, size:10}; num:当前页 ,默认从1开始; size:每页数据条数,默认10
    upCallback: function(page) {
      //联网加载数据
      let $this = this;
      $this.loadData(
        page.num,
        page.size,
        function(curPageData) {
          //curPageData=[]; //打开本行注释,可演示列表无任何数据empty的配置

          //如果是第一页需手动制空列表 (代替clearId和clearEmptyId的配置)
          console.log(curPageData);
          if (page.num == 1) $this.list = [];
          if (curPageData.list) {
            curPageData.list.map(item => {
              $this.list.push(item);
            });
          }
          //联网成功的回调,隐藏下拉刷新和上拉加载的状态;
          //mescroll会根据传的参数,自动判断列表如果无任何数据,则提示空;列表无下一页数据,则提示无更多数据;
          //   console.log("page.num="+page.num+", page.size="+page.size+", curPageData.length="+curPageData.rows.length+", $this.goodsList.length==" + $this.pdlist.length);
          console.log(page);

          //方法二(推荐): 后台接口有返回列表的总数据量 totalSize
          $this.mescroll.endBySize(
            curPageData.list ? curPageData.list.length : 0,
            curPageData.count
          ); //必传参数(当前页的数据个数, 总数据量)
        },
        function() {
          //联网失败的回调,隐藏下拉刷新和上拉加载的状态;
          $this.mescroll.endErr();
        }
      );
    }
  },
  mounted() {
    document.title = "我的团队";
    // this.startMescroll();
    if(this.$route.query.type == 1){
      this.creatEchart();
    }else{
      this.active = 1;
      setTimeout(() => {
        this.startMescroll();
      },500);
    }
  }
};
</script>
<style lang="scss" scoped>
.myTeam {
  background: #fff;
  height: 100vh;
  .mid1 {
    width: 100vw;
    height: calc(100vh - 50px);
  }
  .list {
    background: #fff;
    height: calc(100vh - 50px);

    .item {
      padding: 10px 0;
      .flex {
        display: flex;
        align-items: center;
        border-bottom: 1pt solid #efefef;
        padding: 0 10px;
      }
      .left {
        margin: 0 5px 0 0;
        img {
          padding: 0 10px;
          width: 25px;
        }
        .index {
          width: 40px;
          padding: 0 5px;
          background: yellow;
          color: #666;
          line-height: 25px;
          font-size: 10px;
          border-radius: 0 20px 20px 0;
        }
      }
      .mid {
        img {
          border-radius: 50%;
          margin-right: 10px;
          width: 50px;
          height: 50px;
        }
      }
      .right {
        flex: 2;
        display: flex;
        align-items: center;
        line-height: 30px;
        &-left {
          flex: 2;
          font-size: 15px;
          .name {
            font-weight: bold;
          }
          .time {
            font-size: 13px;
            color: #999;
          }
        }
        &-right {
          width: 60px;
          font-size: 13px;
          text-align: right;
          .label {
            color: #999;
          }
          .num {
            color: $base-color;
            font-weight: bold;
          }
        }
      }
    }
  }
}
</style>